/*******************************************************/
/******************* ## Default Style ******************/
/*******************************************************/


* {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}

:root {
  --nalsoft-base-font: 'Inter', serif;
  --nalsoft-heading-font: 'Hanken Grotesk', serif;
  --nalsoft-primary-color: #FC5546;
  --nalsoft-heading-color: #0B0C0C;
  --nalsoft-base-color: #5B5B5B;
  --nalsoft-lighter-color: #F3F6F9;
  --nalsoft-white-color: #fff;
  --nalsoft-gray-color: #F3F6F9;
  --nalsoft-blue-color: #021433;
  --nalsoft-navyblue-color: #151F39;
  --nalsoft-border-color: rgba(2, 20, 51, 0.1);
  --nalsoft-primary-rgb: 252, 85, 70;
}

body {
  font-weight: 400;
  background: white;
  line-height: 30px;
  color: var(--nalsoft-base-color);
  font-size: 16px;
  font-family: var(--nalsoft-base-font);
}

a {
  color: inherit;
  cursor: pointer;
  outline: none;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  text-decoration: none;
}

a:hover,
a:focus,
a:visited {
  text-decoration: none;
  outline: none;
}

a:hover {
  color: var(--nalsoft-primary-color);
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
  display: inline-block;
}

mark {
  color: var(--nalsoft-primary-color);
  background: transparent;
  text-decoration: underline;
}

header:after,
section:after,
footer:after {
  display: block;
  clear: both;
  content: "";
}

/*======= Input Styles =======*/
input,
select,
textarea,
.nice-select,
.form-control {
  width: 100%;
  height: auto;
  font-weight: 500;
  border-radius: 0;
  padding: 12px 20px;
  background-color: #fff;
  border: 1px solid var(--nalsoft-border-color);
  font-family: var(--nalsoft-heading-font);
}

input:focus,
select:focus,
textarea:focus,
.nice-select:focus,
.form-control:focus {
  outline: none;
  -webkit-box-shadow: none;
  box-shadow: none;
  border-color: var(--nalsoft-heading-color);
}

input:focus::-webkit-input-placeholder,
select:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder,
.nice-select:focus::-webkit-input-placeholder,
.form-control:focus::-webkit-input-placeholder {
  color: var(--nalsoft-heading-color);
}

input:focus:-ms-input-placeholder,
select:focus:-ms-input-placeholder,
textarea:focus:-ms-input-placeholder,
.nice-select:focus:-ms-input-placeholder,
.form-control:focus:-ms-input-placeholder {
