/** Button style **/
.theme-btn,
a.theme-btn {
  z-index: 1;
  position: relative;
  overflow: hidden;
  color: white;
  cursor: pointer;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  font-weight: 600;
  font-size: 18px;
  text-align: center;
  padding: 15px 44px;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  font-family: var(--nalsoft-base-font);
  text-transform: capitalize;
  background: var(--nalsoft-primary-color);
  border-radius: 25px;
}

.theme-btn::after,
a.theme-btn::after {
  position: absolute;
  left: -25%;
  top: 100%;
  width: 150%;
  padding-top: 150%;
  content: '';
  z-index: -1;
  background: black;
  opacity: .1;
  border-radius: 50%;
  -webkit-transition: ease-in .5s;
  -o-transition: ease-in .5s;
  transition: ease-in .5s;
}

.theme-btn i,
a.theme-btn i {
  margin-left: 7px;
}

.theme-btn:hover::after,
a.theme-btn:hover::after {
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

@media only screen and (max-width: 575px) {

  .theme-btn,
  a.theme-btn {
