/* Dropdown Menu Styles */
.main-menu .navbar-collapse li.dropdown {
  position: relative;
}

.main-menu .navbar-collapse li.dropdown .sub-menu {
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 280px;
  background: #fff;
  border: 1px solid var(--nalsoft-border-color);
  border-radius: 12px;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.12);
  opacity: 0;
  visibility: hidden;
  transform: translateY(-15px) scale(0.95);
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  z-index: 9999;
  padding: 15px 0;
  list-style: none;
  margin: 0;
  overflow: hidden;
}

.main-menu .navbar-collapse li.dropdown:hover .sub-menu,
.main-menu .navbar-collapse li.dropdown.active .sub-menu,
.main-menu .navbar-collapse li.dropdown .sub-menu.show {
  opacity: 1 !important;
  visibility: visible !important;
  transform: translateY(0) scale(1) !important;
  display: block !important;
}

.main-menu .navbar-collapse li.dropdown .sub-menu li {
  padding: 0;
  margin: 0;
  border: none;
  display: block;
  opacity: 0;
  transform: translateX(-10px);
  animation: slideInFromLeft 0.3s ease forwards;
}

.main-menu .navbar-collapse li.dropdown .sub-menu li:nth-child(1) { animation-delay: 0.05s; }
.main-menu .navbar-collapse li.dropdown .sub-menu li:nth-child(2) { animation-delay: 0.1s; }
.main-menu .navbar-collapse li.dropdown .sub-menu li:nth-child(3) { animation-delay: 0.15s; }
.main-menu .navbar-collapse li.dropdown .sub-menu li:nth-child(4) { animation-delay: 0.2s; }
.main-menu .navbar-collapse li.dropdown .sub-menu li:nth-child(5) { animation-delay: 0.25s; }
.main-menu .navbar-collapse li.dropdown .sub-menu li:nth-child(6) { animation-delay: 0.3s; }
.main-menu .navbar-collapse li.dropdown .sub-menu li:nth-child(7) { animation-delay: 0.35s; }
.main-menu .navbar-collapse li.dropdown .sub-menu li:nth-child(8) { animation-delay: 0.4s; }
.main-menu .navbar-collapse li.dropdown .sub-menu li:nth-child(9) { animation-delay: 0.45s; }
.main-menu .navbar-collapse li.dropdown .sub-menu li:nth-child(10) { animation-delay: 0.5s; }
.main-menu .navbar-collapse li.dropdown .sub-menu li:nth-child(11) { animation-delay: 0.55s; }

@keyframes slideInFromLeft {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.main-menu .navbar-collapse li.dropdown .sub-menu li a {
  display: block;
  padding: 15px 25px;
  color: var(--nalsoft-heading-color);
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  border-bottom: 1px solid #f8f9fa;
  position: relative;
  overflow: hidden;
}

.main-menu .navbar-collapse li.dropdown .sub-menu li:last-child a {
  border-bottom: none;
}

.main-menu .navbar-collapse li.dropdown .sub-menu li a::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(252, 85, 70, 0.1), transparent);
  transition: left 0.5s ease;
}

.main-menu .navbar-collapse li.dropdown .sub-menu li a:hover::before {
  left: 100%;
}

.main-menu .navbar-collapse li.dropdown .sub-menu li a:hover {
  color: var(--nalsoft-primary-color);
  background-color: #f8f9fa;
  text-decoration: none;
  transform: translateX(5px);
  padding-left: 30px;
}

/* Force show dropdown when show class is present */
.main-menu .navbar-collapse li.dropdown .sub-menu.show {
  opacity: 1 !important;
  visibility: visible !important;
  transform: translateY(0) !important;
  display: block !important;
}

/* Mobile dropdown styles */
@media only screen and (max-width: 991px) {
  .main-menu .navbar-collapse li.dropdown .sub-menu {
    position: static;
    opacity: 1;
    visibility: visible;
    transform: none;
    box-shadow: none;
    border: none;
    background: #f9f9f9;
    margin-top: 10px;
    border-radius: 0;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease;
  }
  
  .main-menu .navbar-collapse li.dropdown .sub-menu.show {
    max-height: 500px;
  }
  
  .main-menu .navbar-collapse li.dropdown .sub-menu li {
    opacity: 1;
    transform: none;
    animation: none;
  }
  
  .main-menu .navbar-collapse li.dropdown .sub-menu li a {
    padding: 12px 30px;
    font-size: 16px;
    transition: all 0.2s ease;
  }
  
  .main-menu .navbar-collapse li.dropdown .sub-menu li a:hover {
    transform: none;
    padding-left: 30px;
  }
}

/* Submenu toggler arrow */
.submenu-toggler {
  display: inline-block;
  margin-left: 8px;
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  position: relative;
  width: 16px;
  height: 16px;
}

.submenu-toggler::after {
  content: '▼';
  font-size: 10px;
  color: var(--nalsoft-heading-color);
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  line-height: 1;
}

.main-menu .navbar-collapse li.dropdown:hover .submenu-toggler::after,
.main-menu .navbar-collapse li.dropdown.active .submenu-toggler::after {
  transform: translate(-50%, -50%) rotate(180deg);
  color: var(--nalsoft-primary-color);
}

.main-menu .navbar-collapse li.dropdown:hover .submenu-toggler {
  transform: scale(1.1);
}

/* Dropdown trigger hover effect */
.main-menu .navbar-collapse li.dropdown:hover > a {
  color: var(--nalsoft-primary-color);
  transform: translateY(-1px);
}

.main-menu .navbar-collapse li.dropdown.active > a {
  color: var(--nalsoft-primary-color);
}

/* Add a subtle glow effect to active dropdowns */
.main-menu .navbar-collapse li.dropdown.active .sub-menu {
  box-shadow: 0 20px 50px rgba(252, 85, 70, 0.15);
  border-color: rgba(252, 85, 70, 0.2);
}

/* Hero carousel height adjustments */
.hero-slide {
  min-height: calc(100vh - 60px) !important;
}

/* Responsive carousel height adjustments */
@media only screen and (max-width: 991px) {
  .hero-slide {
    min-height: calc(100vh - 70px) !important;
  }
}

@media only screen and (max-width: 767px) {
  .hero-slide {
    min-height: calc(100vh - 60px) !important;
  }
}

@media only screen and (max-width: 575px) {
  .hero-slide {
    min-height: calc(100vh - 50px) !important;
  }
}

/* Hero slider dots (match NalsoftWebApp style) */
.hero-text-slider {
  position: relative;
}
.hero-text-slider .slick-dots {
  position: absolute;
  left: 50%;
  bottom: 28px;
  transform: translateX(-50%);
  z-index: 3;
  margin: 0;
  /* constrain to content */
  display: inline-flex;
  width: auto;
  padding: 2px 6px;
  border-radius: 9999px;
  background: rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(2px);
}
.hero-text-slider .slick-dots li {
  width: 10px;
  height: 10px;
  background: rgba(255, 255, 255, 0.45);
  border: 2px solid rgba(255, 255, 255, 0.65);
  border-radius: 50%;
  transition: all 0.3s ease;
}
.hero-text-slider .slick-dots li button {
  opacity: 0;
}
.hero-text-slider .slick-dots li:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 18px;
  height: 18px;
  margin-top: -9px;
  margin-left: -9px;
  border-radius: 50%;
  border: 2px solid var(--nalsoft-primary-color);
  opacity: 0;
  transform: scale(0.8);
  transition: all 0.3s ease;
}
.hero-text-slider .slick-dots li.slick-active {
  background: var(--nalsoft-primary-color);
  border-color: var(--nalsoft-primary-color);
  box-shadow: 0 0 0 4px rgba(252, 85, 70, 0.15);
}
.hero-text-slider .slick-dots li.slick-active:after {
  opacity: 1;
  transform: scale(1);
}

/* Better contrast when background is light */
.hero-text-slider.light-dots .slick-dots li {
  background: rgba(0, 0, 0, 0.25);
  border-color: rgba(0, 0, 0, 0.4);
}
.hero-text-slider.light-dots .slick-dots li.slick-active {
  background: var(--nalsoft-primary-color);
  border-color: var(--nalsoft-primary-color);
}

@media only screen and (max-width: 767px) {
  .hero-text-slider .slick-dots {
    bottom: 18px;
  }
  .hero-text-slider .slick-dots li {
    margin: 0 5px;
  }
}